import React, { Component } from "react";
import "./index.scss";
import Swipe from "../../components/swipe/index";
import { NavBar, Icon, ActionSheet, WingBlank, WhiteSpace, Button, Toast } from 'antd-mobile';
import { observer } from "mobx-react";
import { history } from "../../../utils/history"
@observer
class Read extends Component {
  static defaultProps = {
    fontsize: 16,
    color: "#red",
    background: "#fec"
  }

  constructor(props) {
    super(props);
    this.state = {
      fontsize: 16,
      color: "#red",
      background: "#fec"
    };
  }
  changeColor = (e) => {
    this.refs.word.value=e.target.value;
        this.setState({
          color:this.refs.word.value
        })
        this.refs.mybook.style.color=this.refs.word.value
       
  }

  changeBackGround=(e)=>{
    this.refs.back.value=e.target.value;
    this.setState({
      background:this.refs.back.value
    })
    this.refs.mybook.style.background=this.refs.back.value
  }

  increase=()=>{
    this.refs.mybook.style.fontSize=(++this.state.fontsize)+"px"
  console.log(this.refs.mybook.style.fontSize)
   
  }
  descrease=()=>{
    this.refs.mybook.style.fontSize=(--this.state.fontsize)+"px"
    console.log(this.refs.mybook.style.fontSize)
   
  }


 

  font = () => {
    console.log(12341234)
  }

  render() {
   

    const { fontsize } = this.props
    const { children } = this.state

    const myobj = JSON.parse(sessionStorage.obj)

    return (
      <div className="readme" style={{ position: "relative" }}>

        <div className="readBody"
          onTouchStart={this.getout}

          style={{ width: "100%", minHeight: "625px", fontSize: "20px", position: "relative" }}>
          <div style={{ width: "100vw", height: "1rem", position: "fixed", top: "0", left: "0" }}>  <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={() => { history.go(-1) }}
            rightContent={[

            ]}
          >{myobj.title}</NavBar></div>

          {/* 底部的样式 */}
          <div className="myfoot">
            <ul className="oUl">
              <li>
                <p><img src="http://book.easou.com/jsp/online/resources/imgs/font.png" alt="" /></p>
                <p style={{ width: "107px" }}><span
                ref="big"
                 onClick={this.increase}>+</span><span>字体大小</span><span
                 ref="small"
                  onClick={this.descrease}>-</span></p>
              </li>
              <li>
                <p>
                  <input type="color" id="word" ref="word" onChange={this.changeColor} />
                </p>
                <p>字体颜色</p>
              </li>
              <li>
                <p>
                  <input type="color" id="word"
                  
                   ref="back"
                   onChange={this.changeBackGround} />
                </p>
                <p>背景颜色</p>
              </li>
              <li>
                <p>
                  <img src="http://book.easou.com/jsp/online/resources/imgs/category.png" alt="" />
                </p>
                <p>
                  目录
                                </p>
              </li>
            </ul>
          </div>


<div className="mycolor" ref="mybook" style={{fontSize:"16px"}}>
            
          <div style={{ zIndex: "500" }}><h3 style={{ fontWeight: "bold", fontSize: "22px", marginTop: "37px" }}>第1章小云</h3><p></p><p>　春暖花开时节，窗外的一枝迎春花探出半开的窗子，合着温暖的春风，在午后令人昏昏欲睡。</p><p>　安静的茶水间外，突然传来了轻快的脚步，不大一会儿，青布帘子被挑开，走进来了一个穿着一身碧绿春衫，梳着小小发髻的女孩子。</p><p>　这女孩子年岁不大，唇红齿白，目光灵动，瞧见了茶水间里正靠在一张小凳子上昏昏欲睡的红衣女孩儿，急忙笑了一下上前拍了她一下说道，“小云，还不起来。”</p><p>　她这一声叫脸色微微苍白的女孩儿张开了眼睛，她看清楚了眼前的是谁，不由露出了一个浅浅的柔和笑容，从正坐在小茶炉上的茶壶里倒了一碗茶水来，压低了声音给了这女孩子轻声问道，“你怎么过来了？”</p><p>　她的声音有些微弱，脸色发白，有些病容。</p><p>　虽然穿着一件粗布的红色的裙子，可是就算是粗糙的布料，却挡不住她的一张十分精致漂亮的脸。</p><p>　她看起来不过八九岁的样子，却比刚刚进门的女孩子多了几分稳重。</p><p>　“我是来叫你去领赏的。”绿衣女孩儿一口喝了茶，对她感激一笑，这才急忙说道，“刚儿外头传来了喜信儿，说是咱们三爷中了探花了。老太太心里高兴，因此咱们院子里的小丫鬟子每人都赏半吊钱。这可不是喜事儿？”</p><p>　她顿了顿，忍不住四处看了看顿足说道，“莺儿只怕已经过去了。偏把你留</p></div><div style={{ zIndex: "499", marginBottom: "50px" }}><p>在这儿看着炉子。叫我说，一样儿的三等一样儿的差事，凭什么她处处显眼儿，却只叫你一个人在这看着炉子，从不在外头走动。”</p><p>　“我如今精神短，且谁做这些活儿都是一样儿的。”红衣女孩儿笑着说道。</p><p>　“这才说你是傻子，你偏不信。她日日争你的风头，看炉子的是你，可是捧着茶水往老太太面前去卖好儿，讨喜，得赏钱的怎么就不是你了？”</p><p>　见红衣女孩儿只是柔和地笑了笑，并不也自己分辨，也不义愤填膺，绿衣女孩儿不由垂头叹了一口气说道，“你就是心肠软，又不爱与她计较。只是若是不在主子面前露脸，平日里只守着咱们那点儿月钱才有多少。都是一样儿的人。她生的还不如你呢。”</p><p>　“好了。”红衣女孩儿名叫云舒，见眼前与自己同年纪的绿衣女孩儿翠柳脸色都不开心了起来，急忙说道，“今日是府里的大喜事，你应该高兴才对。半吊钱，这可不是白得的？只是这茶水间离不得人。你帮我去领了，先放在你那儿。晚上回去了你再给我。”</p><p>　她与翠柳都是老太太院子里的三等小丫鬟，住在后头的大通铺里，一间屋子里住着八个三等的小丫鬟，都是今年刚送到老太太院子里来服侍的新人。</p><p>　八个小丫鬟差不多都是八九岁的年纪，虽然做不得老太太房中精细的活儿，可是看茶水，喂鸟扫院子，也不累，只是得精心些。</p><p>　至于一些粗糙的活儿，比如烧火，或是洗衣等等，都有粗使的婆子，也不需要这些娇滴滴的小丫鬟们做。</p><p>　与她一块儿分在茶水间里看茶水的还有一个名叫莺儿的小丫鬟，也与云舒年</p></div><div ><p>纪相仿，只是一向机灵，且有些心计，喜欢往老太太的屋儿里去奉承，就算服侍不上老太太，也愿意在老太太身边几个养尊处优，给老太太房中管着许多内外事的大丫鬟面前露脸。</p><p>　这府中的老太太乃是富贵双全的人，对下人也一向都宽容大方，莺儿时常在老太太的面前晃，不是抢着给端茶倒水，就是打帘子扫地的，因此在三等丫鬟里算得上是得意人。</p><p>　老太太屋里的大丫鬟手头儿都是散漫的，莺儿侍候得好，因此平白也会多给她些赏钱。</p><p>　或是些铜钱，或是一两块点心，更大方些，赶上莺儿讨人喜欢了，许还是几寸的衣裳料子，或者大丫鬟们看不上的小首饰。</p><p>　云舒却不大喜欢往老太太的屋里去的。</p><p>　她从来都没有想到，自己一眨眼醒过来，会变成了一个百年世族唐国公府的三等小丫鬟，卖身为奴不说，还失去了自由。</p><p>　这唐国公府虽然富贵无双，在这京城之中乃是数一数二的豪门世家，可是叫云舒说，宁愿去做个乡间日夜劳作的农女，也不愿做这富贵风流窝里低人一等的丫鬟。</p><p>　她穿过来的时候还在生病，许是这一场病厉害，才将从前的那个可怜的小丫鬟给烧得没了，叫云舒得了这个病恹恹的身体。</p><p>　好不容易将养好了，云舒也没有心情去主子们的面前服侍，只守着茶水间倒是清闲。这养了一段时间的病，如今才叫好些，因此倒是精神了些，也慢慢地</p></div><div ><p>习惯了自己的身份。</p><p>　与刚刚醒过来的时候发现成为生死半点不由自己的丫鬟的惊扰与烦闷，到现在的安然自处，云舒也想明白了许多。</p><p>　既来之则安之。</p><p>　虽然说是做了丫鬟，可就当是在古代找了一份工作，认真地好好儿做事，攒钱，日后赶上主子们的恩典放了自己的身契出去做个良民，且拿着在国公府里攒的月钱与赏赐多买些良田，自己做个悠闲的小地主，倒是极好的。</p><p>　因想到了这些，云舒再想到唐国公府中的三爷这次中了探花就每个小丫鬟赏了半吊钱，不由在心底暗暗地感慨了一下国公府中的富庶。</p><p>　她一个三等的做不得什么累活儿的小丫鬟，一个月的月钱都有半吊钱，如今一次赏赐，就叫她多了一个月的月钱。</p><p>　如今在京城周围的良田云舒已经问过府中有经验的婆子，听说一亩良田十两银子。</p><p>　她一年的月钱与赏赐加一块儿，说不得就能攒下来两亩良田。</p><p>　日积月累，等她长大些，可不就是一个小地主了？</p><p>　到时候几亩田地，一个院子，春华秋实倒是也悠闲极了。</p><p>　想到这里，云舒急忙推了推翠柳说道，“快去吧。别耽搁了，也误了你的赏钱。”</p><p>　“可是前头不仅在分赏钱，还在分点心。听说是云片糕。”翠柳见云舒不肯过去，只埋头干活儿，不由顿足说道，“莺儿那丫头做事跑在最后头，赏钱分</p></div><div ><p>点心分果子却总是她头一份儿。只你好脾气，总是吃亏。这茶水见里没人照看儿一会儿怎么了？今日也没听说有客人会来。”</p><p>　老太太屋儿里的茶水间是离不的人的，不仅是老太太与一并在老太太面前侍奉的夫人小姐要时常用热茶，就是唐国公府这样显赫的世家，时常有内外贵族女眷来上门做客，总不能叫茶水间里没人，炉火灭了叫客人喝凉茶。</p><p>　今日没听说府中接了谁的登门的帖子，因此翠柳就劝了一句。</p><p>　云舒却只是摇头。</p><p>　她在现代的时候做工作早就知道，做什么都得认真负责，断断没有因为“不会发生”，就偷懒儿的。</p><p>　叫她说，国公府既然付给自己薪水，那自己就应该认真地工作，而不是偷奸耍滑。</p><p>　“你多吃点儿，把我那份儿吃回来就好了。”她推了推有些不肯的翠柳，也忍不住弯起眼睛笑了。</p><p>　她虽然年幼，才八九岁上的年纪，可是生得眉目似画，十分美丽。只是许是因只做着三等的小丫鬟，她也不在脸上十分涂脂抹粉，因此瞧着有些脸色暗暗的。翠柳素日里劝云舒打扮些，只是云舒倒是觉得做一个小丫鬟还是低调些，闷声发大财才好。</p><p>　她对果子不感兴趣，只对那半吊钱感兴趣。</p><p>　毕竟这可是往后叫她后半辈子衣食无忧的依靠。</p><p>　翠柳到底顿足，点了点她的额头转身风风火火地走了。</p><p></p></div><div ><p>　云舒重新坐回茶炉前，看着茶炉里跳跃的一点火苗儿，沉吟了一会儿，摸出了收在腰间的一个小荷包来。</p><p>　这荷包敞开，倒了倒，倒在她如今小小的雪白的掌上的是几块儿碎银子，掂量了几下，大概能有个五两。</p><p>　这就是从前那个小丫鬟小云的全部的家底儿了。</p><p>　她卖身进了府中的时间也不长，因此没有攒下什么家底。这五两银子已经是不少的了。云舒看了看这可怜的五两银子，心里又叹了一口气，小心地把她收拢在自己的荷包里重新放回了腰间。</p><p>　她是不大能相信自己如今睡的那个大通铺的，八个小丫鬟都在一块儿，平日里来来往往的人都不少，当初小云是孤身一人被卖进了国公府之中，不及别的小丫鬟是国公府里的家生子，父母兄弟姐妹都在国公府中当差，等了赏钱能交给家中的长辈，不会放在大通铺。</p><p>　云舒没有家人在这府里看顾，因此凡事也多了几分小心。</p><p>　不过从前的小云就是一个老实性子，素日里也不大往大丫鬟与主子的面前凑，攒的不多，因此也不打眼儿。</p><p>　她想到这里，想到自己也听人说起，如今这个世道，只要有个两三百亩的田地，就能做一个生活得很富庶不发愁的小地主，因此心里倒是有了个目标</p><p>　不管怎样，先努力攒钱，当个地主婆。</p></div></div>


</div>

      </div>
    )

  }
}

export default Read;