import React, { Component } from "react"
import "./index.scss"
import { SearchBar, InputItem, Button, WhiteSpace, Icon, WingBlank, Grid } from 'antd-mobile';
import Head from "../../components/head";
import MyData from "../../mobx/data"
import { axios } from "../../../utils/axios";
import { history } from "../../../utils/history";


class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            searchlist: [],
            keyword: "",
        }
    }


    changeword = () => {
        this.state.keyword = this.refs.searchlista.value


    }

    getdata = () => {
        var thkeyword = this.state.keyword;
        var thkeyword = this.state.keyword;
        let that = this;
        //  function getme(){
        axios.get("/react/gosearch", {
            params: {
                keyword: thkeyword
            }
        }).then(response => {
            const data = response.data;
            this.state.searchlist = data.result
            console.log(this.state.searchlist);
            that.setState({
                searchlist:this.state.searchlist
            })
        }).catch((error) => {
            console.log(error);

        })
        // }


    }

    back=()=>{
        history.go(-1);
    }
    componentDidMount() {

    }


    render() {

        const { searchlist, keyword } = this.state;
        console.log("0000");
        
        console.log(searchlist);
        
        const data1 = searchlist.map((_val, i) => ({
            icon: _val.iconUrl,
            title: _val.title
        }));



        return (
            <div style={{background:"white",height:"100%",width:"100vw"}}>
                <div className="content" style={{ height:"7vh",background:"white"}}>
                    <span 
                    className="back"

                    onClick={this.back}
                   >返回</span>
                    <input maxLength={20}
                        style={{ width: "58%", float: "left", height: "0.8rem", textIndent: "10px" }}
                        onChange={this.changeword}
                        ref="searchlista"
                        placeholder="书名/作者/关键字"
                    /><Button type="primary"
                        onClick={this.getdata}
                        style={{ width: "20%", float: "left" }} inline
                    >搜索</Button>

                </div>

                <WhiteSpace />

                <div>
                    <Grid data={data1}
                        columnNum={3}
                        renderItem={dataItem => (
                            <div style={{ padding: '12.5px', display: "flex", justifyContent: "center", flexDirection: "column" }}>
                                <img src={dataItem.icon} style={{ width: '75px', height: '85px', marginLeft: "20px" }} alt="" />
                                <div style={{ color: '#888', fontSize: '14px', marginTop: '12px', textAlign: "center" }}>
                                    <span>{dataItem.title}</span>
                                </div>
                            </div>
                        )}
                    />
                </div>
            </div>
        )
    }
}

export default Search
