import React,{Component} from "react";
import { NavBar, Icon , Popover} from 'antd-mobile';
import "./index.scss";
import {history} from "../../../utils/history"
import {inject,observer} from "mobx-react";
@inject("store") @observer
class Head extends Component {
    constructor(props){
        super(props)
        this.state={
            light:true
        }
    }

    goBack=()=>{
        history.go(-1)
    }
    toSearch=()=>{

        // 跳转到路由为search的页面s
        history.push("/search");
    }

    // 灯光ian
    
    toSun=()=>{
      this.state.light=!this.state.light;
       this.props.store.flag=!this.state.light;

       console.log(this.props.store.flag);
        if(this.state.light==true){
        this.refs.mysun.className="iconfont icon-taiyang"
   
       }else{
        this.refs.mysun.className="iconfont icon-yueliang";
        // 这个地方最好使用mobx
    }
    }




    render(){

        const {
            title,
            show,
            elip,
            isSearch,
            out,
            store     
        } =this.props
        return(
            <div className="head">
                
            <NavBar
                mode="light"
                icon={show?<Icon type="left" />:""}
                onLeftClick={() => show?this.goBack():console.log("")
                }
                rightContent={[
                    isSearch&&<Icon key="0" type="search" 
                    onClick={this.toSearch}
                    style={{ marginRight: '16px' }} />,
                    elip&&<Pop key="1" />,
                   !(isSearch)&&<i onClick={this.toSun} 
            
                 ref="mysun"
                  className="iconfont icon-yueliang" key="1" style={{color:"black!important",margin:10,fontSize:20}}/>,
                   !isSearch
                   && <i  key="9"  className="iconfont icon-shezhi" style={{color:"black",margin:10,fontSize:20}}/>,

                ]}
    >{title}</NavBar>




            </div>
        )
    }
}



let Item = Popover.Item;

let wrapProps;




const myImg = src => <img src={`https://gw.alipayobjects.com/zos/rmsportal/${src}.svg`} className="am-icon am-icon-xs" alt="" />;

class Pop extends Component{
    constructor(){
        super();
       this.state = {
            visible: false,
            selected: '',
            };

    }


 onSelect = (opt) => {
    // console.log(opt.props.value);
    this.setState({
      visible: false,
      selected: opt.props.value,
    });
  };

handleVisibleChange = (visible) => {
    this.setState({
      visible,
    });
  };

  render(){
      return(
         <div>
            <NavBar
        mode="light"
        rightContent={
          <Popover mask
            overlayClassName="fortest"
            overlayStyle={{ color: 'currentColor' }}
            visible={this.state.visible}
            overlay={[
              (<Item key="1" value="update" >
              <i className="iconfont icon-zuixingengxin"></i>
              更新连载书</Item>),



              (<Item key="2" value="downLoad" style={{ whiteSpace: 'nowrap' }}> 
               <i className="iconfont icon-download"></i>
              下载管理</Item>),


              (<Item key="3" value="input" >
                <span style={{ marginRight: 5 }}>
                <i className="iconfont icon-daoru"></i>
                本地导入</span>
              </Item>),


               (<Item key="4" value="transform" >
                <span style={{ marginRight: 5 }}>
                <i className="iconfont icon-wifi"></i>
                WIFI传书</span>
              </Item>),


               (<Item key="5" value="manage" >
               <i className="iconfont icon-zhengshu" ></i>
                <span style={{ marginRight: 5 }} >书籍管理</span>
              </Item>),
            ]}
            align={{
              overflow: { adjustY: 0, adjustX: 0 },
              offset: [-10, 0],
            }}
            onVisibleChange={this.handleVisibleChange}
            onSelect={this.onSelect}
          >
            <div style={{
              height: '100%',
              padding: '0 15px',
              marginRight: '-15px',
              display: 'flex',
              alignItems: 'center',
            }}
            >
              <Icon type="ellipsis" />
            </div>
          </Popover>
        }
      >
      
      </NavBar>
         </div>
      )
  }

}



export default Head