import React, { Component } from "react";
import "./index.scss";
import { Grid } from "antd-mobile";
import MyFoot from "../../components/foot";
import Head from "../../components/head";
import MyData from "../../mobx/data"
import { observer } from "mobx-react"
import { history } from "../../../utils/history"
import { axios } from "../../../utils/axios"


@observer
class BookStore extends Component {
    constructor() {
        super();
        this.state = {
            mybook: []
        }
    }
    toDetail(v) {

        // history.push({pathname: `/detail/${v.id}/frombookstore`, state: {data: v}})
        // console.log(v.id);
        history.push({ pathname: `/detail/${v.id}/frombookstore`, state: { data: v } })
        console.log(v.id);

    }



    componentDidMount() {

        axios.get("/react/getallstore").then(
            res => {

                this.setState({
                    mybook: res.data.result
                })
console.log(this.state.mybook)

            }
        )



    }


    render() {

        const { mystorelist } = MyData;
        const { mybook } = this.state;
        const data1 = mybook.map((_val, i) => ({
            icon: _val.cover,
            title: _val.title,
            id: _val._id
        }));

        // console.log(MyData.mystorelist)
        return (
            <div className="container" style={{marginTop:0}}>
                <Head title="让阅读成为习惯" elip={true} isSearch={true} />

                <div className="mystore">

                    <div className="sub-title"></div>
                    <Grid data={data1}
                        columnNum={3}
                        renderItem={dataItem => (
                            <div
                                onClick={() => this.toDetail(dataItem)}
                                style={{ padding: '12.5px', display: "flex", justifyContent: "center", flexDirection: "column" }}>
                                <img src={dataItem.icon} style={{ width: '75px', height: '85px', marginLeft: "20px" }} alt="" />
                                <div style={{ color: '#888', fontSize: '14px', marginTop: '12px', textAlign: "center" }}>
                                    <span>{dataItem.title}</span>
                                </div>
                            </div>
                        )}
                    />
                </div>
            </div>

        )
    }
}



export default BookStore