import React, { Component } from "react";
import "./index.scss";
import Head from "../../components/head";
import { Tabs, WhiteSpace, Badge, Icon } from 'antd-mobile';
import Swipe from "../../components/swipe";
import { observer } from "mobx-react";
import MyData from "../../mobx/data"
import { Grid } from "antd-mobile"
import { history } from "../../../utils/history"


@observer
class BookCity extends Component {
    componentDidMount() {
        
        if (!MyData.girlList.length > 0) {
            MyData.getGirlBooks();
        }
        if (!MyData.boyList.length > 0) {
            MyData.getBoyBooks();
        }
    }

    toSearch=()=>{
        history.push("/search")
    }

    toDetail1(v){

        history.push({pathname: `/detail/${v.id}/fromboy`, state: {data: v}})
        console.log(v.id);
        
    }
    toDetail2(v){

        history.push({pathname: `/detail/${v.id}/fromgirl`, state: {data: v}})
        console.log(v.id);
        
    }
    render() {
        const { jplist,girlList,boyList} = MyData;

        let tabs = [
         
            { title: <Badge >女生</Badge> },
            { title: <Badge >男生</Badge> },
           

            { title: <Icon key="0" type="search" onClick={this.toSearch} style={{ marginRight: '16px' }} /> },
        ];

  

        let children2 = [
            require("../../../assets/images/woman1.jpg"),
            require("../../../assets/images/woman2.jpg"),
            require("../../../assets/images/woman3.jpg"),
        ]

        let children3 = [
            require("../../../assets/images/man0.jpg"),
            require("../../../assets/images/man2.jpg"),
            require("../../../assets/images/man3.jpg"),
        ]



        const data2 = girlList.reverse().slice(0,12).map((_val, i) => ({
            icon: _val.cover,
            title: _val.title,
            author: _val.author,
            id:_val._id
        }));
        const data3 = boyList.reverse().slice(0,12).map((_val, i) => ({
            icon: _val.cover,
            title: _val.title,
            author: _val.author,
            id:_val._id,
        }));
        

    
        
        return (
            <div className="bookCity" >
                <Tabs tabs={tabs}
                    style={{ marginBottom: "1rem" }}
                    initialPage={1}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                >

                    {/* 选项卡的第一项 */}
              


                    <div style={{  display: 'flex', alignItems: 'center', justifyContent: 'center',
                        flexDirection:"column",
                        paddingBottom:"60px",
                        backgroundColor: '#fff' }}>
                        <Swipe children={children2}
                        autoplay={true} dots={true} 
                         style={{marginBottom:"50px"}}/>

                        {/* 女生推荐 */}
                        <Grid data={data2}
                        columnNum={4}
                        style={{width:"100vw"}}
                        renderItem={dataItem => (
                            <div 
                            onClick={()=>this.toDetail2(dataItem)}
                            style={{ 
                           display: "flex", justifyContent: "center", flexDirection: "column" }}>
                                <img src={dataItem.icon} style={{ width: '75px', height: '90px', marginLeft: "20px" }} alt="" />
                                <div style={{ color: '#888', fontSize: '14px', marginTop: '12px', textAlign: "center" }}>
                                    <span>{dataItem.title}</span>
                                    <span>{dataItem.author}</span>
                                </div>
                            </div>
                        )}
                    />
                    </div>


                    <div style={{
                        display: 'flex', alignItems: 'center', justifyContent: 'center',
                        flexDirection:"column",
                        paddingBottom:"60px",
                        backgroundColor: '#fff'
                    }}>
                        <Swipe children={children3}
                        autoplay={true} dots={true} 
                         style={{marginBottom:"50px"}}/>

                       <Grid data={data3}
                        columnNum={4}
                        style={{width:"100vw"}}
                        renderItem={dataItem => (
                            <div
                            onClick={()=>this.toDetail1(dataItem)}
                             style={{ 
                            height:"115px",display: "flex", justifyContent: "center", flexDirection: "column" }}>
                                <img src={dataItem.icon} style={{ width: '75px', height: '90px', marginLeft: "20px" }} alt="" />
                                <div style={{ color: '#888', fontSize: '14px', marginTop: '12px', textAlign: "center" }}>
                                    <span>{dataItem.title}</span>
                                    <span>{dataItem.author}</span>
                                </div>
                            </div>
                        )}
                    />
                    </div>


              
   

                </Tabs>
            </div>



        )
    }
}



export default BookCity