import axios from "axios";

import  {Toast} from "antd-mobile";

import {history} from "./history";


// 设置基路径

export const baseURL="http://yamiyami.online:1909";

var token="";


// 将token设置到基路径里面去、
axios.defaults.baseURL=baseURL;
axios.defaults.headers.common['token']=token;
// 请求的类型
axios.defaults.headers.post['Content-Type']='application/x-www-form-urlencoded';



export function loadingToast(msg) {
    Toast.loading(msg, 3, () => {
      console.log('Load complete !!!');
    });
}
export function successToast(msg) {
    Toast.hide();
    Toast.success(msg, 1);
}

export function failToast(msg) {
  Toast.hide();
  Toast.fail(msg, 1);
}

export function offline(msg) {
  Toast.offline(msg, 1);
}





// axios的拦截器
// 请求拦截器
axios.interceptors.request.use(function (config) {
    // 请求发送之前做的事情，如果session有token的话，就是这里面的token，否则就是空
    
    token = sessionStorage.token ?   sessionStorage.token : "";
    config.headers['token']  = token;
    return config;
    loadingToast("努力加载...");
  
    
  }, function (error) {
    // 请求发送 失败 
    offline("请求失败---网络错误!")
    return Promise.reject(error);
    
  });


//   响应拦截器
axios.interceptors.response.use(function (response) {
    // 获取到响应数据做的事情
    console.log(response)
    setTimeout(() => {
      successToast(response.data.msg);
      
    },1000);

    // 一旦后端返回的是400就跳转到登录
    if(response.data.code=="400"){
      history.push("/login");
    }

    
    return response;

  }, function (error) {
    failToast("服务器error-响应失败!!!");
    return Promise.reject(error);
});


export {axios}



