import UploadImg from "../../components/upLoadImg/index"
import React, { Component } from "react";
import "./index.scss";
import {history} from "../../../utils/history"
export class Mylogin extends Component{
    toLogin=()=>{
        console.log(13132134);
        history.push("/login")
    }

    constructor(){
        super();
        this.state={
            text:"登录/注册",
        }
    }
    render() {
        const {text}=this.state;
        const token=sessionStorage.token;
        const mobile=sessionStorage.mobile;
        
        return (
            <div className="gologin">
          
                <div className="pic">
                <UploadImg></UploadImg>
                </div>
        {!token&&<div className="toLogin" onClick={this.toLogin}>{text}</div>}
        {token&&<div className="toLogin" onClick={this.toLogin}>{mobile}</div>}
              
              
            </div>
        )
    } 
}
