

// 引入路由主件

import {HashRouter,Route} from "react-router-dom";

import React,{Component} from "react";

import Layout from "./views"
export class MainRouter extends Component{
    render(){
        return(
            <HashRouter basename="">
                {/* <h2>app-app-app</h2> */}
                <Route  component={Layout} />
            </HashRouter>
        )
    }
}