// 轮播图组件

import React, { Component } from "react";

import "./index.scss";

import { Carousel } from 'antd-mobile';


class Swipe extends Component {
    constructor() {
        super();
        this.state = {

        }
    }

    static defaultProps = {
        show: true
    }
    render() {

        const { children, autoplay, dots } = this.props
        return (
            <div className="container">
                <Carousel
                    autoplay={autoplay}
                    infinite
                    dots={dots}
                    beforeChange={(from, to) => console.log()}
                    afterChange={index => console.log()}
                    className="banner"
                >
                    {children.map((item, index) => (
                        <a
                            key={index}
                            style={{ display: 'inline-block', width: '100vw', height: "2rem", }}
                        >

                            {/* 一定要写一个src属性，不然图片出不来 */}
                            {<img src={item}
                                style={{
                                    width: '100%',
                                    height: "100%",
                                }}
                                onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));

                                }}
                            />
                            }

                          


                            {/* 如果到了最后一个页面的轮播图，name点击进入按钮就可以进入到书城里面了 */}

                        </a>
                    ))}
                </Carousel>

            </div>
        )
    }
}


export default Swipe

