import { observable, action } from "mobx";
import { axios } from "../../utils/axios"
// import "../../setupProxy"
class MyData {
    @observable flag = false;
    
    @observable.deep changelight = {
        display: "none",
        background: "#0B0D0E",
        color: "#CCD0D2"
    };
    @observable.deep storelist = [];
    @observable.deep jplist = [];
    @observable.deep girlList = [];
    @observable.deep boyList = [];
    @observable.deep mystorelist = [];

    // 获取暑假里面的内容
    @action getStoreList = () => {
        axios.get("/react/getStoreList").then(res => {
            this.storelist = res.data.result;

        })

    }


    // 获取精品书籍
    @action getJingpin = () => {
        axios.get("/react/jinpin").then(res => {
            this.jplist = res.data.result;
            console.log(this.jplist)
        })

    }

    // 获取女生书籍
    @action getGirlBooks = () => {
        axios.get("/react/girlbook").then(res => {
            this.girlList = res.data.result;
            console.log(this.girlList)
        })
    }
     // 获取男生书籍
     @action getBoyBooks = () => {
        axios.get("/react/boybook").then(res => {
            this.boyList = res.data.result;
            console.log(this.boyList )
        })
    }
// 点击加入书架
@action getMyStore=(bid)=>{
    axios.get('/react/getstore',{
        params:{
            bid:bid
        }
    }).then(res=>{
        this.mystorelist=res.data.result;
        console.log(this.mystorelist)
    })
    
}
}

export default new MyData()