import React, { Component } from "react";
// import { Link } from 'react-router';
import "./index.scss";
import { axios } from "../../../utils/axios";
// import { Carousel } from 'antd-mobile';

import { Card, WhiteSpace, Grid, Button } from 'antd-mobile';
import MyData from "../../mobx/data";
import { history } from "../../../utils/history"
import { observer } from "mobx-react";
@observer

class Detail extends Component {


    constructor(props, context) {
        super(props, context);
        this.state = {
            id: this.props.location.pathname.split("/detail/")[1],
            from: this.props.location.pathname.split("/detail/")[1].split('/')[1],
            obj: {},
            commment: [],
        }

    }

    componentDidMount() {
        if (!MyData.storelist.length > 0) {
            MyData.getJingpin();
        }
        if (!MyData.girlList.length > 0) {
            MyData.getGirlBooks();
        }
        if (!MyData.boyList.length > 0) {
            MyData.getBoyBooks();
        }

        if (this.state.from === "frombookstore") {
            axios.get("/react/getStoredetail", {
                params: {
                    id: this.state.id.split("/")[0]
                }
            }).then(res => {
                this.setState({
                    obj: res.data.result[0]
                })
                console.log(res.data.result);
               

            })


        }
        if (this.state.from === "fromboy") {
            axios.get("/react/getboydetail", {
                params: {
                    id: this.state.id.split("/")[0]
                }
            }).then(res => {
                this.setState({
                    obj: res.data.result[0]
                })


            })

      
        }
       
     

        if (this.state.from === "fromgirl") {
            axios.get("/react/getgirldetail", {
                params: {
                    id: this.state.id.split("/")[0]
                }
            }).then(res => {
                this.setState({
                    obj: res.data.result[0]
                })
                console.log(this.state.obj)
            })


        }
    }
  
    goback = () => {
        history.go(-1);
    }
    addTostore = (e) => {
        e.preventDefault();
        // JSON.parse(sessionStorage.obj.bookid);
         // 点击加入书架
        MyData.getMyStore(JSON.parse(sessionStorage.obj).bid);
         console.log(JSON.parse(sessionStorage.obj).bid)
        history.push('/main/bookStore')

       
    }
    keepread = () => {
        history.push("/read")
      
    }
    toDetail(v){

        history.push({pathname: `/detail/${v.id}/frombookstore`, state: {data: v}})
        console.log(v.id);
        window.location.reload();
        
    }
    toDetail1(v){

        history.push({pathname: `/detail/${v.id}/fromboy`, state: {data: v}})
        console.log(v.id);
        window.location.reload();
        
    }
    toDetail2(v){

        history.push({pathname: `/detail/${v.id}/fromgirl`, state: {data: v}})
        console.log(v.id);
        window.location.reload();
    }


    render() {
        const { id, from, obj, commment } = this.state;
        const { storelist, girlList, boyList } = MyData;

        sessionStorage.obj=JSON.stringify(storelist)

        const data1 = storelist.reverse().slice(0,9).map((_val, i) => ({
            icon: _val.cover,
            title: _val.title,
            author: _val.author,
            id: _val._id
        }));




        const data2 = girlList.reverse().slice(0,9).map((_val, i) => ({
            icon: _val.cover,
            title: _val.title,
            author: _val.author,
            id: _val._id
        }));
        const data3 = boyList.reverse().slice(0,9).map((_val, i) => ({
            icon: _val.cover,
            title: _val.title,
            author: _val.author,
            id: _val._id,
        }));

        return (
            <div className="toDetail" >
                <Card full style={{ background: "rgba(88, 54, 224, 0.8)", height: "4.5rem" }} scoped>
                    <Card.Header
                        title={<span onClick={this.goback} style={{ color: "white" }}>返回</span>}
                        style={{ color: "white!important" }}
                        thumb="https://gw.alipayobjects.com/zos/rmsportal/MRhHctKOineMbKAZslML.jpg"
                        extra={<span onClick={this.addTostore} style={{ color: "white" }}>加入书架</span>}
                    />
                    <Card.Body>
                        <div>
                            <div className="top">
                                <div className="topleft">
                                    <p className="p1">{obj.title}</p>
                                    <p className="p2">{obj.author}</p>
                                    <p className="p3">{obj.tags}</p>
                                    <span>字数:{obj.words}字</span>
                                    <span>读者:{obj.reads}在读</span>
                                </div>
                                <div className="topright">
                                    <img src={this.state.obj.cover} />
                                </div>
                            </div>
                        </div>
                    </Card.Body>

                </Card>


                {/* 简介 */}

                <Card full className="myjianjie">
                    <Card.Header
                        title="简介"
                        style={{ padding: "10px 30px", borderBottom: "2px solid #eee", fontSize: "20px" }}

                    />
                    <br />
                    <Card.Body>
                        <div className="jianjie">
                            <span>简介:</span>
                            <div style={{ letterSpacing: "2px", lineHeight: "26px" }}>{obj.desc}</div>
                        </div>
                    </Card.Body>

                </Card>
                <p>大家都在看</p>
                {from=="frombookstore" && <Grid data={data1}
                    columnNum={3}
                    style={{ width: "100vw" }}
                    renderItem={dataItem => (
                        <div
                            onClick={() => this.toDetail(dataItem)}
                            style={{
                                padding: '8px',
                                height: "115px", display: "flex", justifyContent: "center", flexDirection: "column"
                            }}>
                            <img src={dataItem.icon} style={{ width: '75px', height: '85px', marginLeft: "20px" }} alt="" />
                            <div style={{ color: '#888', fontSize: '14px', marginTop: '12px', textAlign: "center" }}>
                                <span>{dataItem.title}</span>
                                <span>{dataItem.author}</span>
                            </div>
                        </div>
                    )}
                />}

               {from=="fromgirl" && <Grid data={data2}
                    columnNum={3}
                    style={{ width: "100vw" }}
                    renderItem={dataItem => (
                        <div
                            onClick={() => this.toDetail2(dataItem)}
                            style={{
                                padding: '8px',
                                height: "115px", display: "flex", justifyContent: "center", flexDirection: "column"
                            }}>
                            <img src={dataItem.icon} style={{ width: '75px', height: '85px', marginLeft: "20px" }} alt="" />
                            <div style={{ color: '#888', fontSize: '14px', marginTop: '12px', textAlign: "center" }}>
                                <span>{dataItem.title}</span>
                                <span>{dataItem.author}</span>
                            </div>
                        </div>
                    )}
                />}

               {from=="fromboy"&&<Grid data={data3}
                    columnNum={3}
                    style={{ width: "100vw" }}
                    renderItem={dataItem => (
                        <div
                            onClick={() => this.toDetail1(dataItem)}
                            style={{
                                padding: '8px',
                                height: "115px", display: "flex", justifyContent: "center", flexDirection: "column"
                            }}>
                            <img src={dataItem.icon} style={{ width: '75px', height: '85px', marginLeft: "20px" }} alt="" />
                            <div style={{ color: '#888', fontSize: '14px', marginTop: '12px', textAlign: "center" }}>
                                <span>{dataItem.title}</span>
                                <span>{dataItem.author}</span>
                            </div>
                        </div>
                    )}
                />}
                <div className="myfoot" style={{
                    position: "fixed", bottom: "0",
                    left: "0", display: "flex", sjustifyContent: "space-between"
                }}>
                    <Button type="warning" inline onClick={this.addTostore} style={{ width: "50vw" }}>加入书架</Button>
                    <Button style={{ width: "50vw" }} type="primary" inline onClick={this.keepread}> 继续阅读</Button>
                </div>
            </div>
        )
    }
}


export default Detail


