import React, { Component } from "react";
import "./index.scss";
import { axios } from "../../../utils/axios"
import { history } from "../../../utils/history"
import Head from "../../components/head";
import {WhiteSpace,WingBlank,List,InputItem,Button} from "antd-mobile";
export const tmobile = /^1(3|5|7|8|9)\d{9}$/
export const tcode = /^\d{6}$/
var timer = null

class Login extends Component {
    constructor() {
        super()
        this.state = {
            mobileFlag: false,
            loginFlag: false,
            text: "发送验证码",
            count: 60,
            toggle: true,
        }
    }

     /* 试下手机验证码的登录逻辑 
                 //校验手机号和验证码,如果手机号验证合，就解除发送验证码的禁用，如果都合格解除登录的禁用,在这里直接传入mobile就可以触发事件
      */
checkMobile = (mobile) => {
    console.log(this.state.toggle)
    if (this.state.toggle) {
        this.setState({
            mobileFlag: tmobile.test(mobile),
            loginFlag: tcode.test(this.code.state.value) && tmobile.test(mobile)

        })
    }
}

// 验证验证码
checkCode=(code)=>{
    console.log(this.code.state);

    this.setState({
        loginFlag: tcode.test(code) && tmobile.test(this.mobile.state.value)
    })
}



// 验证数据库验证码是否和收到的验证码是否一致
checkLogin=()=>{
    axios.post("/react/checkCode",{
        code:this.code.state.value*1,
        mobile:this.mobile.state.value*1,
    }).then(res=>{
     
        // 根据返回的type判断结果,token 和手机号
        if(!!res.data.type){
            sessionStorage.token=res.data.token;
            sessionStorage.mobile=this.mobile.state.value;
            // 成功的话就跳转页面
            history.push("/main/mine")
        }else{
            sessionStorage.token="";
            
        }
    })
}
// 点击发送验证码，将手机号存入数据库，并且获取token

sendCode=()=>{
    axios.post("/react/getCode",{mobile:this.mobile.state.value*1}).then(
        res=>{
            console.log(res);
        }
    )


this.computedCount();
}

// 店家发送验证码后倒计时且不可以点击
  // 定时器实现倒计时
  computedCount = () => {
    // 初始化的时候也需要改变一下，不然会有延时
    this.setState({
        count: --this.state.count,
        text: `${this.state.count}s 后重新发送`,
        mobileFlag: false,
    })
    timer = setInterval(() => {
        if (this.state.count > 0) {
            this.setState({
                count: --this.state.count,
                text: `${this.state.count}s 后重新发送`,
                mobileFlag: false,
                toggle: false
            })
            console.log(this.state.toggle);

        } else {
            clearInterval(timer)
            this.setState({
                mobileFlag: true,//可以发送验证码
                text: "发送验证码",
                count: 10,
                toggle: true
            })
        }
    }, 1000)
}

render() {
    const {
        text,
        mobileFlag,
        loginFlag
    }=this.state

    return (
        <div className="login">
            
                <Head title="登录" isSearch={true}></Head>
      
            <WhiteSpace />
            <div>
            <List>
                <WingBlank>
                    <div ></div>
                    <InputItem
                        defaultValue=""
                        placeholder="请输入手机号"
                        data-seed="logId"
                        ref={el => this.mobile = el}
                        onChange={this.checkMobile}
                    >手机号</InputItem>

                    <div style={{
                        display: "flex", justifyContent: "spaceBetween",
                        alignItems: "center"
                    }}>
                        <InputItem
                            defaultValue=""
                            placeholder="请输入验证码"
                            data-seed="logId"
                            ref={el => this.code = el}
                            onChange={this.checkCode}
                        >验证码</InputItem>
                        <Button type="warning"
                            inline
                            disabled={!mobileFlag}
                            size="small"
                            onClick={this.sendCode}
                            style={{ marginRight: '4px', width: "150px" }}>{text}</Button>
                    </div>
                    <Button type="primary"
                        onClick={this.checkLogin}
                        disabled={!loginFlag}>登录</Button>
                    <WhiteSpace />
                </WingBlank>
            </List>
            </div>
          


        </div>
    )
}
}

export default Login;