import React,{Component} from "react";

import "./index.scss";

import {Switch,Route,Redirect} from "react-router-dom";

import BookCity from "../bookCity";
import BookStore from "../bookStore";
import Classify from "../classify";
import Mine from "../mine";
import MyFoot from "../../components/foot";
import LazyLoad from "../../../utils/lazyLoad"
import {inject,observer} from "mobx-react";
@inject("store") @observer
 class Main extends Component{
    render(){
      const {store} =this.props
        return(
          <div className="main " style={store.flag? {width:'100vw',height:'100vh',background:"#0B0D0E",zIndex:10000,color:"#CCD0D2!important"}:{width:'100vw',height:'100vh'}}>
              

              <Switch>
                    <Route path="/main/" exact render={()=>(<Redirect to="/main/bookCity"/>)}/>
                   

                  <Route path="/main/bookCity" component={BookCity}/>

                  <Route path="/main/bookStore" component={BookStore}/>


                  {/* <Route path="/main/bookStore" component={LazyLoad(()=>(import("../bookStore")))}/> */}

                    <Route path="/main/classify" component={Classify}/>

                  <Route path="/main/mine" component={Mine}/>


                    {/* 输入错误也是进入书城
                     */}

                     <Route render={()=>(<Redirect to="/main/bookCity"/>)}/>
              </Switch>
              <MyFoot></MyFoot>
              </div>
        )}
}

export default Main