import React, { Component } from "react";
import "./index.scss";
import Head from "../../components/head";
import { Accordion, List } from 'antd-mobile';

import {inject,observer} from "mobx-react";
import {Mylogin} from "../mylogin"
@inject("store") @observer
class Mine extends Component {

    onChange = (key) => {
        console.log(key);
      }
    render() {
        const {store} =this.props
     
        return (
            <div className="mine"  style={store.flag? {width:'100vw',background:"#0B0D0E",zIndex:10000,color:"#CCD0D2!important"}:{width:'100vw'}}>

                <Head elip={false} isSearch={false} />
                <div   >

                    <Mylogin className="login" />


                    <ul className="oul">
                        <li>
                            <p className="num">0</p>
                            <p>书豆</p>
                        </li>
                        <li>
                            <p className="num">0</p>
                            <p>书券</p>
                        </li>
                        <li>
                            <p className="num">0</p>
                            <p>章节券</p>
                        </li>
                        <li>
                            <p className="num">0</p>
                            <p>月票</p>
                        </li>
                    </ul>

                    <div className="picture">
                        <img src="http://img-tailor.11222.cn/pm/book/operate/2018020715462184.jpg" alt="" />
                    </div>
                </div>

                <ul className="middle">
                    <li>
                        <i className="iconfont  icon-xiaoxi"></i>
                        <p>消息</p>
                    </li>
                    <li>
                        <i className="iconfont  icon-shoucang1"></i>
                        <p>收藏</p>
                    </li>
                    <li>
                        <i className="iconfont icon-pinglun"></i>
                        <p>书评</p>
                    </li>
                    <li>
                        <i className="iconfont icon-shijian"></i>
                        <p>阅历</p>
                    </li>
                </ul>


                <div style={{ marginTop: 10, marginBottom: 10,
                background:"none" }}>
                    <Accordion defaultActiveKey="0" className="my-accordion" onChange={this.onChange}>
                        <Accordion.Panel header="绑兑换码">
                        </Accordion.Panel>
                        <Accordion.Panel header="公益3小时" className="pad">
                        </Accordion.Panel>
                        <Accordion.Panel header="阅读喜好" className="pad">
                        </Accordion.Panel>
                        <Accordion.Panel header="意见反馈" className="pad">
                        </Accordion.Panel>
                        <Accordion.Panel header="邀请有礼" className="pad">
                        </Accordion.Panel>
                    </Accordion>
                </div>
            </div>
        )
    }
}


export default Mine