import React,{Component} from "react";

import "./index.scss";

import {NavLink} from "react-router-dom"

const footMenu=[
    {txt:"书城",name:"bookCity",path:"/main/bookCity",icon:"icon-ai-book"},
    {txt:'分类',name:"classify",path:"/main/classify",icon:"icon-leimupinleifenleileibie2"},
    {txt:"书架",name:"bookStore",path:"/main/bookStore",icon:"icon-book"},
    {txt:"我的",name:"mine",path:"/main/mine",icon:"icon-wode"},
]



class MyFoot extends Component{
     constructor(){
        super();
        this.state={footMenu};
    }


    render(){
        return(
            <div>
              <footer>
                {
                    this.state.footMenu.map((item,index)=>{
                        return (
                            <div key={index} className="content">
                             <NavLink
                                    to={item.path}
                                    activeClassName="nav-active"
                                >
                                    <i className={"iconfont "+item.icon}></i>
                                    <span>
                                        {item.txt}
                                    </span>
                                </NavLink>
                            </div>
                        )
                    })
                }
              </footer>
            </div>
        )
    }
}

export default MyFoot;